<template>
  <div id="app">
    <router-view></router-view>
    <div id="bottom" :style="bottomH">
      <section v-if="homebottom" class="HomeBeian">
        <span>CopyRight©2020 Tzw</span>
        <span>
          <a href="http://www.beian.miit.gov.cn" style="color: white;margin-left: 20px;">滇ICP备20002333号-1</a>
        </span>
      </section>
      <section v-else class="OtherBeian">
        <span>CopyRight© tzw</span>
        <span>
          <a href="http://www.beian.miit.gov.cn" style="color: white;margin-left: 20px;">滇ICP备20002333号-1</a>
        </span>
      </section>
    </div>
    <header id="guide" :style="bkcolor">
      <blog-guide></blog-guide>
    </header>
  </div>
</template>

<script>
  import Guide from './components/function/Guide.vue'
  export default {
    name: 'App',
    components: {
      'blog-guide': Guide
    },
    data:function() {
      return {
        bkcolor: '',
        bottomH: {
          height:'200px'
        },
        homebottom: true,
        backimg: ''
      }
    },
    mounted() {
      this.ChangeGuide();
    },
    watch: {
      '$route': 'ChangeGuide'
    },
    methods: {
      ChangeGuide:function() {
        var e = this.$route.path;
        var b = document.getElementById('bottom');
        if(e === '/bloghome') {
          this.bkcolor = {background: ''};
          this.bottomH.height = '200px';
          this.homebottom = true;
        }
        else {
          this.bkcolor = {backgroundColor: 'white'};
          this.bottomH.height = '50px';
          this.homebottom = false;
        }
        // console.log(e);
      }
    }
}
</script>

<style>
  body {
    margin: 0px;
    padding: 0px;

  }
  #app {
    background: url(http://101.37.83.157:3000/images/300295.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
  #guide {
    position: fixed;
    top: 0px;
    left: 0px;
    height: 60px;
    width: 100%;
    /* border: 1px red solid; */
    background: rgba(255,255,255,1);
  }
  #bottom {
    background-color: #303133;
    padding-top: 10px;
  }
  .HomeBeian {
    margin: 100px 20% 0px 20% ;
    /* border: 1px red solid; */
    height: 50px;
    color: white;
    text-align: center;
    font-size: 17px;
    letter-spacing: 1px;
  }
  .OtherBeian {
    margin: 7px 20% 0px 20%;
    /* border: 1px red solid; */
    height: 30px;
    color: white;
    text-align: center;
  }
</style>



